<template>
  <div class="cart">
    <!-- 顶部导航 -->
    <van-nav-bar title="购物车" left-text="返回" left-arrow @click-left="onClickLeft" fixed>
      <template #right>
        <van-icon name="weapp-nav" />
      </template>
    </van-nav-bar>
 
    <div class="cart-box" v-for='(item,index) in list' :key='item._id'>
      <van-swipe-cell>
        <van-row type="flex" justify="center" align="center">
          <van-col span="4">
            <van-cell>
                <van-checkbox @change='chuan(index, $event)' v-model="item.checked" />
            </van-cell>
          </van-col>
          <van-col span="20">
            <van-card
              :num="item.num"
              :desc="item.good.desc"
              :price="item.good.price"
              :title="item.good.name"
              class="goods-card"
              :thumb="item.good.img"
            >
              <template #footer>
                <van-button size="mini" @touchstart='upDown ("sub",item)' >-</van-button>
                <van-button size="mini" @touchstart='upDown ("add",item)'>+</van-button>
              </template>
            </van-card>
          </van-col>
        </van-row>
        <template #right>
          <van-button square text="删除" type="danger" class="delete-button" @click="DeleteCartsGoods(item)" />
        </template>
      </van-swipe-cell>
    </div>

    <van-submit-bar :price="jiage" button-text="提交订单" @submit="onSubmit">
      <van-checkbox  v-model='checkbox' @click='all(full)'>全选</van-checkbox>
      <template #tip>
        你的收货地址不支持同城送,
        <span @click="onClickEditAddress">修改地址</span>
      </template>
    </van-submit-bar>
  </div>
</template>

<script>
import {
  NavBar,
  Icon,
  Card,
  SwipeCell,
  Button,
  Col,
  Checkbox,
  Cell,
  Row,
  SubmitBar,
  Toast
} from "vant";
import{mapState,mapActions,mapMutations,mapGetters} from 'vuex'
export default {
  name: "cart",
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,
    [SwipeCell.name]: SwipeCell,
    [Card.name]: Card,
    [Button.name]: Button,
    [Col.name]: Col,
    [Checkbox.name]: Checkbox,
    [Cell.name]: Cell,
    [Row.name]: Row,
    [SubmitBar.name]: SubmitBar,
    [Toast.name]:Toast,
  },
  data: function() {
    return {
      checkbox:true
    };
  },
  computed:{
    ...mapState('cart', ['list']),
    ...mapGetters('cart',['full','jiage'])
  },
  mounted(){
    this. getList()
  },
  methods: {
    ...mapActions('cart',['getList']),
    ...mapMutations('cart',['upList','upListIt','cheAll']),
    //返回首页,
    onClickLeft() {
      this.$router.replace("/s");
    },
    all(a){
      this.cheAll(a)
    },
    // 获取checked
    chuan(index,check){
      this.upListIt({index,check}) //拿到相应checked的值
      this.checkbox=this.full
    },
    //修改数量
    upDown(type,item){
      if(type=='sub'&& item.num==1) return Toast('商品数量不能为0')
      let data={
        id:item._id,
        num:type=='add'?item.num+1 :item.num-1
      }
      this.$http.updateCartsNum(data).then(()=>{
        Toast("修改成功") 
        this. getList()
      })
    },
    // 删除购物车商品
    DeleteCartsGoods(item){
      let params={
        id:item._id
      }
      this.$http.DeleteCartsGoods(params).then(res=>{
        Toast(res)
        this.getList()
      })
    },
    // 提交订单
    onSubmit() {},
    // 修改地址
    onClickEditAddress() {}
  }
};
</script>

<style lang="scss" scoped>
.cart {
  padding: 2rem 0 1.6rem 0;
  .van-card {
    padding: 0;
    background: #ffffff;
  }
  .delete-button {
    height: 100%;
  }
}
</style>